.NotBeingAttacked {
    position: relative;
}

.IsBeingAttacked {
    position: relative;
}

.Pokemon {
    height: 80%;
    width: 80%;
    margin-left: 15%;
}

.PokemonAttacking {
    height: 80%;
    width: 80%;
    margin-left: 5%;
    animation: attack .5s linear;
}

.Attack {
    font-size: 0px;
    height: 100%;
    width: 100%;
    position: absolute;
    background-size: 100% 100%;
    transform: translate(-130px, -20px);
}

.AttackNot {
    display: none;
}

@keyframes attack {
    0% {transform: translate(0px, 0px);}
    50% {transform: translate(-600px, 0px);}
    100% {transform: translate(0px, 0px);}
}