.LetterBank {
    display: grid;
    grid-template-columns: repeat(26, auto);
    grid-column: 1/3;
}

.Letter {
    font-size: 40px;
    border: 1px solid black;
    background-color: white;
    border-radius: 8px;
    cursor: pointer;
}

.LetterClicked {
    font-size: 40px;
    border: 1px solid rgba(255, 255, 255, 0);
    background-color: rgba(255, 255, 255, 0);
    border-radius: 8px;
}

.Letter:hover {
    transform: scale(1.5);
}