.PokemonHangmanBody {
    position: absolute;
    top: 0%;
    left: 0%;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 15% 60% 15%;
    background-image: url('../Pictures/pokemonBackgroundOne.png');
    background-size: 100% 100%;
    height: 100vh;
}

.Victory {
    position: absolute;
    display: grid;
    grid-template-columns: 100%;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100vh;
    grid-template-rows: 60% 45%;
    background-image: linear-gradient(to right, blue, green, red);
}

.Victory img {
    justify-self: center;

}

.Victory p {
    justify-self: center;
    font-size: 100px;
    font-weight: 900;
    background-color: rgba(204, 192, 88, 0.74);
    border-radius: 16px;
    padding: 0px 10px 0px 10px;
}

.Defeat {
    position: absolute;
    display: grid;
    grid-template-columns: 100%;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100vh;
    grid-template-rows: 60% 45%;
    background-image: linear-gradient(to bottom, purple, grey, black);
}

.Defeat img {
    justify-self: center;

}

.Defeat p {
    justify-self: center;
    font-size: 100px;
    font-weight: 900;
    color: white;
    border-radius: 16px;
    padding: 0px 10px 0px 10px;
}

.ScreenBleed {
    position: absolute;
    width: 100%;
    height: 100%;
    transition: ease-in-out;
    animation: bleed 4s;
}






@keyframes bleed {
    0% { background-image: radial-gradient(transparent 0%, transparent 60%, green 100%);}
    25% { background-image: radial-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0));}
    50% { background-image: radial-gradient(transparent 0%, transparent 60%, green 100%);}
    75% { background-image: radial-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0));}
    100% { background-image: radial-gradient(transparent 0%, transparent 60%, green 100%);}
}


