.Container {
    display: grid;
    grid-template-columns: repeat(9, auto);
}

.ImageDiv {
    border: 1px solid black;
    height: 75px;
    width: 75px;
    border-radius: 50%;
    background-image: linear-gradient(to bottom, rgb(146, 2, 2) 30%, black 50%, white 70% );
}

.Image {
    height: 80%;
    width: 80%;
    margin: 10% 0px 0px 10%;
}