.Container {
    text-align: center;
    display: grid;
    grid-template-columns: 15% 85%;
    grid-template-rows: 15% 85%;
    padding-top: 20px;
    grid-gap: none;
}

.HitPoints{
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    background-color: rgba(250, 128, 114, 0.275);
    border-radius: 16px;
    padding: 6px;
    grid-row: 1/2;
    grid-column: 2/3;
    width: 60%;
    margin: 0% 0px 0px 15%;
}

.Heart {
    height: 50px;
    width: 50px;
    justify-self: center;
}

.Pokemon {
    height: 60%;
    width: 60%;
    grid-row: 2/3;
    grid-column: 2/3;
    margin: 5% 0px 0px 15%;
    transform: rotateY(180deg);
}
.PokemonDying {
    height: 60%;
    width: 60%;
    grid-row: 2/3;
    grid-column: 2/3;
    margin: 5% 0px 0px 15%;
    animation: animate 2s;
}
.AshHolder {
    grid-row: 2/3;
    grid-column: 1/2;
}

.Ash {
    width: 100%;
    height: 90%;
}

.Pokeball {
    position: absolute;
    top: 49%;
    left: 5%;
    width: 2.5%;
    height: 6%;
}

.PokeballThrow {
    position: absolute;
    top: 49%;
    left: 5%;
    width: 2.5%;
    height: 6%;
    animation: throw 3s linear;
    z-index: 5;
}

@keyframes throw {
    0% {
        transform: translate(0px, 0px) ;

    }
    10% {
        transform: translate(100px, -100px) ;
    }
    20% {
        transform: translate(250px, -150px);
    }
    30% {
        transform: translate(400px, -150px);
    }
    40% {
        transform: translate(550px, -100px);
    }
    50% {
        transform: translate(700px, -50px);
    }
    60% {
        transform: translate(960px, -50px) scale(18);
    }
    70% {
        transform: translate(960px, -50px) scale(18);
    }
    80% {
        transform: translate(400px, 0px);
    }
    90% {
        transform: translate(200px, 0px);
    }
    100% {
        transform: translate(0px, 0px);
    }
}


@keyframes animate {
    0% {height: 0%;
        width: 0%; 
        transform: translate(-100px, 100px);}
    100% { height: 60%;
            width: 60%;
            transform: translate(0px, 0px );
            transform: rotateY(180deg);}
}